import { Icon } from './Icon.js';
export const Button = ({ children, state }) => {
  const style = {
    width: '90px',
    height: '30px',
    fontSize: '12px',
    borderRadius: '15px',
    textAlign: 'left',
    border: '0px',
    boxShadow: '0px 4px 4px #DBDDF0',
  };
  return (
    <button style={style}>
      {children} <Icon>{state}</Icon>
    </button>
  );
};
